import s from './../Sidebar.module.css';

let Friends = (props) => {
  return (
    <div className={s.friends}>
      <div className={s.item}>
        <img src="https://i.pinimg.com/originals/ab/0c/cd/ab0ccd195f693a98973735817e9aa7de.jpg" />
      </div>
      <div>{props.name}</div>
    </div>
  );
};

export default Friends;
