import React from 'react';
import s from './Music.module.css';
import { VideoClips } from './VideoClips/VideoClips';

export const Music = (props) => {
  return (
    <div className={s.music}>
      <div className="s.item">
        <h1>Music</h1>
      </div>
      <div>
        <VideoClips />
      </div>
    </div>
  );
};
